.btn-white{
    border: 1px solid $white;
    background: $white;
    color: $color-primary;
    border-radius: 20px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    transition: all 0.3s ease;
    width: 115px;
    height: 20px;
    padding: 13.5px 20px;
    margin-bottom: 7px;
    margin-top: 23px;

    &.lg {
       width: 204px;
       height: 26px;
       font-size: 26px;
       color: $color-secondary;
       font-family: $regular;
       text-align: center;
       margin-bottom: 45px;
    }
}